.btn {
    position: absolute;
    border: none;
    border-radius: 1rem;
    outline: 0;
    cursor: pointer;
    font-family: Ubuntu;
}

.link {
    text-decoration: none;
}

.btn-primary {
    color: #e5ddf0;
    background-color: #0075f1;
    padding: .8rem 1.8rem;
    font-size: 1.5rem;
    font-weight: bold;
    letter-spacing: .005em;
}

.btn-primary:hover {
    box-shadow:
        3px 3px 10px #0065cf,
        -3px -3px 10px #0065cf;
}