.card-content {
    width: 180px;
    height: 220px;
    margin: auto;
    perspective: 600px;
}

.card {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: 2s;
}

.card:hover {
    transform: rotateY(180deg);
}

.card__side {
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    position: absolute;
    border-radius: 5rem;
    background: linear-gradient(225deg, #c0a0b0, #c3e1e2);
}

.card__side--front {
    display: flex;
    justify-content: center;
}

.card__side--back,
.card__side:hover {
    box-shadow:
        -10px 10px 30px #b5adc5,
        10px -10px 30px #84769c;
}

.card__side--back {
    transform: rotateY(180deg);
    display: flex;
    justify-content: center;
}

.card--name {
    align-self: flex-end;
    position: absolute;
    margin-bottom: 25px;
    font-family: Ubuntu;
    font-size: 22px;
    letter-spacing: .005em;
    text-align: center;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.35);
}

.card--icon {
    width: 100px;
    height: 100px;
    align-self: stretch;
    margin-top: 25px;
}

.card--path {
    align-self: flex-end;
    margin-bottom: 1.3rem;
}

.card--description {
    width: 80%;
    align-self: flex-start;
    margin: 18px auto 5px;
    padding: 0 5px;
    text-align: left;
    font: 13px OpenSans;
    line-height: 22px;
}